<template>
  <div class="login">
    <div>
      <div>
        <br>
        <div class="column">  
          <div class="title is-3 level-item" text-align="center">
            Requisitar um serviço para o(a) {{this.pro.imagem.user.name}}
            <hr>
          </div>
        </div>
        <br>
        <form action method="POST">
          <div class="column is-offset-3 is-6-desktop is-full-mobile">
            <label for="valor" class="label">
              <b>Diga o titulo do serviço</b>
            </label>
            <input
              type="text"
              class="input inp"
              v-model="titulo"
              placeholder="Ex:. Floriano"
              name="valor"
              id="valor"
            >
          </div>
          <div class="columns">
            <div class="control column is-offset-3 is-3-desktop is-full-mobile">
              <li class="column">
                <label for="valor" class="label">
                  <b>Quanto você pretende pagar?</b>
                </label>
                <input
                  type="text"
                  class="input inp"
                  v-model="valor"
                  placeholder="Ex:. Floriano"
                  name="valor"
                  id="valor"
                >
              </li>
            </div>
            <div class="column is-3-desktop is-full-mobile">
              <li class="column">
                <label for="tipo" class="label">
                  <b>Como vai ser o atendimento?</b>
                </label>
                <select
                  class="control column is-12-desktop inp"
                  name="tipo"
                  id="tipo"
                  v-model="tipo"
                >
                  <option value="Presencial">Presencial</option>
                  <option value="Remoto">Remoto</option>
                </select>
              </li>
            </div>
          </div>
          <div class="column is-offset-3 is-6-desktop is-full-mobile">
            <label for="local" class="label">
              <b>Diga o local do serviço</b>
            </label>
            <input
              type="text"
              class="input inp"
              v-model="local"
              placeholder="Ex:. Floriano"
              name="local"
              id="local"
            >
          </div>
          <div class="column is-6-desktop is-offset-3 is-full-mobile">
            <label for="descricao" class="label">
              <b>Descrição</b>
            </label>
            <textarea
              name="descricao"
              id="descricao"
              class="textarea inp"
              v-model="descricao"
              placeholder="Digite suas especialidades, formações ou metas, sua idade etc"
              cols="20"
              rows="5"
            ></textarea>
          </div>
          <div class="control column">
            <button @click.prevent="addProposta"
              class="column is-6-desktop is-offset-3 is-full-mobile btn-1"
            >Enviar Proposta</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import gql from "graphql-tag";
import profissional from "@/graphql/queries/profissional.gql";
import clientForUserId from "@/graphql/queries/clientForUserId.gql";
import addProposta from '@/graphql/mutations/addproposta.gql'

//import {onLogin} from '../vue-apollo.js'
//import {Bus} from '../event.js'
export default {
  data() {
    return {
      me: null,
      pro: [],
      clientes: [],
      titulo: '',
      valor: '',
      local: '',
      descricao: '',
      tipo: ''
    };
  },
  apollo: {
    me: gql`
      query {
        me {
          id
          name
          email
          status
        }
      }
    `,
    pro: {
      query: profissional,
      variables() {
        return {
          id: this.$route.params.id
        };
      },
      update(data) {
        this.pro = data;
        return this.pro;
      }
    },
    clientes: {
      query: clientForUserId,
      variables() {
        return {
          user_id: this.me.id
        };
      },
      update(data) {
        this.clientes = data;
        return this.clientes;
      }
    }
  },
  methods: {
    addProposta() {
      this.$apollo
        .mutate({
          mutation: addProposta,
          variables: {
            titulo: this.titulo,
            valor: this.valor,
            local: this.local,
            descricao: this.descricao,
            tipo: this.tipo,
            status: 0,
            finalizar: 0,
            user_envia_id: this.clientes.clientForUserId[0].id,
            user_recebe_id: this.pro.imagem.id
          }
        })
        .then(data => {
          console.log(data);
          this.$router.push(`/page/cliente/categories/profissional/${this.pro.imagem.id}`);
        })
        .catch(error => {
          console.error(error);
        });
    }
  }
};
</script>
<style>
@import "../../cssFiles/inputsAndButtons.css";

.inp {
  width: 100%;
  height: 3.5em;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp:focus {
  border: 3px solid rgb(94, 10, 190);
}

.inp1 {
  width: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp1:focus {
  border: 3px solid tomato;
}
</style>
